import {Component} from '@angular/core';
import {TranslatePipe} from "ng2-translate/ng2-translate";
import {TranslateService} from "ng2-translate/ng2-translate";
import {LangChangeEvent} from "ng2-translate/ng2-translate";
import {ParagraphTransform} from "../common/pipes/paragraph-transform.pipe";
const _ = require('lodash');

@Component({
  selector: 'home',
  providers: [],
  pipes: [ TranslatePipe, ParagraphTransform ],
  styles: [ require('./home.scss') ],
  template: require('./home.html')
})
export class Home {
  benefits = [];

  constructor(public translateService: TranslateService) {
    this.benefits = _.get(this.translateService.get('home.benefits.tiles'), 'value');
    this.translateService.onLangChange.subscribe((event: LangChangeEvent) => {
      this.benefits = _.get(event.translations, 'home.benefits.tiles', []);
    })
  }

}



