import {Component} from '@angular/core';
import {TranslatePipe} from "ng2-translate/ng2-translate";
import {MATERIAL_DIRECTIVES} from "../../platform/browser/angular2-material2/index";
import {TranslateService} from "ng2-translate/ng2-translate";
import {LangChangeEvent} from "ng2-translate/ng2-translate";
const _ = require('lodash');


@Component({
  selector: 'dojo',
  pipes: [TranslatePipe],
  styles: [require('./dojos.scss')],
  template: require('./dojos.html'),
  directives: [MATERIAL_DIRECTIVES]
})

export class Dojos {
  dojos = [];
  details: Object;
  constructor(public translateService: TranslateService) {
    this.dojos = _.get(this.translateService.get('dojos.dojos'), 'value');
    this.translateService.onLangChange.subscribe((event: LangChangeEvent) => {
      this.dojos = _.get(event.translations, 'dojos.dojos', []);
    })
  }

  itemOnClick(dojo) {
    this.details = dojo;
    window.scrollTo(0, 0);
  }
}



