import {Component} from '@angular/core';
import {CORE_DIRECTIVES} from "@angular/common";
import {DROPDOWN_DIRECTIVES} from "ng2-bootstrap/ng2-bootstrap";
import {TranslatePipe} from "ng2-translate/ng2-translate";
import {TranslateService} from "ng2-translate/ng2-translate";
import {LangChangeEvent} from "ng2-translate/ng2-translate";

const _ = require('lodash');

@Component({
  selector: 'change-lang',
  styles: [require('./change-lang.scss')],
  template: require('./change-lang.html'),
  directives: [DROPDOWN_DIRECTIVES, CORE_DIRECTIVES],
  pipes: [ TranslatePipe ]
})

export  class ChangeLang {
  public currentLang: string;
  constructor(public translateService: TranslateService) {}

  ngOnInit() {
    this.translateService.onLangChange.subscribe((event: LangChangeEvent) => {
      this.currentLang = _.get(event.translations, event.lang);
      localStorage.setItem('lang', event.lang);
    })
  }

  isActive(language: string): boolean {
    return language.toLocaleLowerCase() === this.translateService.currentLang
  }

  setLang(lang) {
    this.translateService.use(lang);
  }
}



