import {Component, ViewContainerRef} from '@angular/core';
import {TranslatePipe} from "ng2-translate/ng2-translate";
import {MATERIAL_DIRECTIVES} from "../../platform/browser/angular2-material2/index";
import {TranslateService} from "ng2-translate/ng2-translate";
import {LangChangeEvent} from "ng2-translate/ng2-translate";
import {CAROUSEL_DIRECTIVES} from "ng2-bootstrap/ng2-bootstrap";
import {Modal} from "angular2-modal/angular2-modal";
const _ = require('lodash');

@Component({
  selector: 'gallery',
  pipes: [TranslatePipe],
  styles: [require('./gallery.scss')],
  template: require('./gallery.html'),
  directives: [MATERIAL_DIRECTIVES, CAROUSEL_DIRECTIVES,]
})

export class Gallery {
  images = [];
  interval = 5000;
  constructor(public translateService: TranslateService, public modal: Modal) {
    this.images = _.get(this.translateService.get('gallery.images'), 'value');
    this.translateService.onLangChange.subscribe((event: LangChangeEvent) => {
      this.images = _.get(event.translations, 'gallery.images', []);
    })
  }

  itemOnClick(index) {
    const imgUrl = this.images[index].url;
    //let modal = this.modal.alert();
    //modal['size'].apply('lg');
    //modal['showClose'].apply(true);
    //modal['body'].apply('<img src="'+imgUrl+'"/>');
    //return modal.open();
    //return this.modal.alert()
    //    .size('lg')
    //    .showClose(true)
    //    .body('<img src="'+imgUrl+'"/>')
    //  .open();
  }
}



